<template>
  <div class="input-with-validation">
    <label v-if="label" :for="'input-' + id">{{ label }}</label>
    <textarea
      :id="'input-' + id"
      v-if="type === 'textarea'"
      class="form-control"
      :class="{ 'is-invalid': error }"
      rows="3"
      :placeholder="placeholder"
      :value="modelValue"
      @input="handleInput"
    >
    </textarea>
    <input
      v-else
      :id="'input-' + id"
      :type="type"
      :placeholder="placeholder"
      :value="modelValue"
      @input="handleInput"
      :class="{ 'is-invalid': error }"
      class="form-control"
    />
    <p v-show="error" class="error-message">{{ error }}</p>
  </div>
</template>

<script>
export default {
  name: 'input-component',
  props: {
    id: String,
    label: String,
    type: {
      type: String,
      default: 'text'
    },
    placeholder: String,
    modelValue: String,
    validation: Function,
    validationStatus: String
  },
  data () {
    return {
      error: null
    }
  },
  methods: {
    handleInput (event) {
      const value = event.target.value
      this.performValidation(value)
      this.$emit('update:modelValue', value)
    },
    performValidation (value) {
      if (this.validation) {
        const validationResult = this.validation(value)
        this.error = validationResult
        this.$emit('update:validationStatus', validationResult)
      } else {
        this.error = null
        this.$emit('update:validationStatus', null)
      }
    }
  },
  watch: {
    validationStatus (newStatus) {
      this.error = newStatus
    }
  }
}
</script>

<style scoped>
.error-message {
  color: red;
  font-size: 0.8em;
  margin-top: 0.2em;
}
.is-invalid {
  border-color: red;
}
</style>
