<template>
  <form action="" @submit.prevent="Save">
    <input-component
      label="Назва фільма"
      id="filmName"
      type="text"
      placeholder="Введіть назву фільма"
      v-model="filmName"
      :validation="validateFilmName"
      :validate="validate"
      :validationStatus="filmNameValidationStatus"
      @update:validationStatus="filmNameValidationStatus = $event"
    />
    <input-component
      class="mt-3"
      label="Дата виходу"
      id="releaseDate"
      type="date"
      placeholder="Введіть дату виходу"
      v-model="releaseDate"
      :validation="validateReleaseDate"
      :validationStatus="releaseDateValidationStatus"
      @update:validationStatus="releaseDateValidationStatus = $event"
    />
    <input-component
      class="mt-3"
      label="Опис"
      id="filmDescription"
      type="textarea"
      placeholder="Введіть опис фільма"
      v-model="filmDescription"
      :validation="validateFilmDescription"
      :validationStatus="filmDescriptionValidationStatus"
      @update:validationStatus="filmDescriptionValidationStatus = $event"
    />
    <img-component v-model="mainPicture" class="mt-3" @deleteMainPicture="deleteMainPicture"></img-component>
    <imgs-component v-model="gallary" class="mt-3" @newImage="addImage" @delImage="delImage" @changeImage="changeImage"></imgs-component>
    <label>Галерея картинок</label>
    <input-component
      class="mt-3"
      label="Силка на трейлер"
      id="trailerLink"
      type="text"
      placeholder="Введіть силку на трейлер в youtube..."
      v-model="trailerLink"
      :validation="validateDefault"
      :validationStatus="trailerLinkValidationStatus"
      @update:validationStatus="trailerLinkValidationStatus = $event"
    />
    <div class="form-group mt-3 ml-1">
      <label>Тип фільма</label>
      <div class="row" :class="{ 'is-invalid': TypeError }">
        <checkbox-component v-model="typeFilm['3D']" label="3D"></checkbox-component>
        <checkbox-component v-model="typeFilm['2D']" label="2D"></checkbox-component>
        <checkbox-component
          v-model="typeFilm.IMAX"
          label="IMAX"
        ></checkbox-component>
      </div>
      <p v-show="TypeError" class="error-message">Виберіть тип фільма</p>
    </div>
    <card-component title="SEO блок" class="mt-3">
      <template #tools></template>
      <template #body>
        <input-component
          label="URL"
          id="SEOURL"
          type="text"
          placeholder="URL..."
          v-model="SEO.URL"
          :validation="validateDefault"
          :validationStatus="SEOURLValidationStatus"
          @update:validationStatus="SEOURLValidationStatus = $event"
        />
        <input-component
          class="mt-3"
          label="Title"
          id="SEOTitle"
          type="text"
          placeholder="Title..."
          v-model="SEO.Title"
          :validation="validateDefault"
          :validationStatus="SEOTitleValidationStatus"
          @update:validationStatus="SEOTitleValidationStatus = $event"
        />
        <input-component
          class="mt-3"
          label="Keywords"
          id="SEOKeywords"
          type="text"
          placeholder="Keywords..."
          v-model="SEO.Keywords"
          :validation="validateDefault"
          :validationStatus="SEOKeywordsValidationStatus"
          @update:validationStatus="SEOKeywordsValidationStatus = $event"
        />
        <input-component
          class="mt-3"
          label="Description"
          id="SEODescription"
          type="text"
          placeholder="Description..."
          v-model="SEO.Description"
          :validation="validateDefault"
          :validationStatus="SEODescriptionValidationStatus"
          @update:validationStatus="SEODescriptionValidationStatus = $event"
        />
      </template>
    </card-component>
    <button type="button" @click="GetFilm" class="btn btn-xs btn-success col fileinput-button">
      <span>Скинути зміни</span>
    </button>
    <button type="submit" class="btn btn-xs btn-success col fileinput-button">
      <span>Зберегти</span>
    </button>
  </form>
</template>

<script>
import inputComponent from '../UI/input-component.vue'
import { CloudStorage } from '@/js/cloudStorage'
import { CloudFirestore } from '@/js/cloudFirestore'
import CardComponent from '../UI/card-component.vue'
import CheckboxComponent from '../UI/checkbox-component.vue'
import imgComponent from '../UI/img-component.vue'
import { doc, collection } from 'firebase/firestore'
import imgsComponent from '../UI/imgs-component.vue'
export default {
  components: {
    inputComponent,
    CardComponent,
    CheckboxComponent,
    imgComponent,
    imgsComponent
  },
  data () {
    return {
      mainPicture: {},
      gallary: [],
      filmName: '',
      releaseDate: '',
      filmDescription: '',
      trailerLink: '',
      typeFilm: {
        '3D': false,
        '2D': false,
        IMAX: false
      },
      TypeError: false,
      SEO: {
        URL: '',
        Title: '',
        Keywords: '',
        Description: ''
      },
      ListDeleteImages: [],
      cloudFirestore: new CloudFirestore(),
      cloudStorage: new CloudStorage(),
      filmNameValidationStatus: null,
      releaseDateValidationStatus: null,
      filmDescriptionValidationStatus: null,
      trailerLinkValidationStatus: null,
      typeFilmValidationStatus: null,
      SEOURLValidationStatus: null,
      SEOTitleValidationStatus: null,
      SEOKeywordsValidationStatus: null,
      SEODescriptionValidationStatus: null,
      mainPictureValidationStatus: null,
      saveListImgTriger: false,
      updateListImgTriger: false,
      isLoading: false
    }
  },
  props: {
    language: String,
    collectionRef: Object,
    id: String
  },
  mounted () {
    this.GetFilm()
  },
  computed: {
    fileFolderPath () {
      return `films/${this.language}/${this.id}`
    },
    Gallary () {
      const gallaryDocRef = doc(this.collectionRef, 'Gallary')
      return collection(gallaryDocRef, 'Gallary')
    }
  },
  async created () {
    this.GetFilm()
  },
  methods: {
    addImage (newImage) {
      this.gallary.push(newImage)
    },
    delImage (id) {
      this.gallary = this.gallary.filter(image => image.id !== id)
    },
    changeImage (newImage) {
      this.gallary = this.gallary.map(image =>
        image.id === newImage.id ? newImage : image)
    },
    deleteMainPicture (value) {
      this.ListDeleteImages.push(value)
    },
    validateFilmName (value) {
      const minLength = 1
      const maxLength = 100
      if (value.length < minLength) {
        return `Назва фільма повинна містити хоча б ${minLength} символ`
      }
      if (value.length > maxLength) {
        return `Назва фільма не повинна перевищувати ${maxLength} символів`
      }
      return null
    },
    validateReleaseDate (value) {
      const inputDate = new Date(value)
      if (isNaN(inputDate)) {
        return 'Будь ласка, введіть коректну дату'
      }
      return null
    },
    validateFilmDescription (value) {
      const minLength = 10
      const maxLength = 1000
      if (value.length < minLength) {
        return `Опис фільма повинен містити хоча б ${minLength} символів`
      }
      if (value.length > maxLength) {
        return `Опис фільма не повинен перевищувати ${maxLength} символів`
      }
      return null
    },
    validateDefault (value) {
      const minLength = 1
      if (value.length < minLength) {
        return 'Поле не може бути пустим'
      }
      return null
    },
    validate () {
      this.filmNameValidationStatus = this.validateFilmName(this.filmName)
      this.releaseDateValidationStatus = this.validateReleaseDate(
        this.releaseDate
      )
      this.filmDescriptionValidationStatus = this.validateFilmDescription(
        this.filmDescription
      )
      this.trailerLinkValidationStatus = this.validateDefault(this.trailerLink)
      this.SEOURLValidationStatus = this.validateDefault(this.SEO.URL)
      this.SEOTitleValidationStatus = this.validateDefault(this.SEO.Title)
      this.SEOKeywordsValidationStatus = this.validateDefault(
        this.SEO.Keywords
      )
      this.SEODescriptionValidationStatus = this.validateDefault(
        this.SEO.Description
      )
      return (
        this.filmNameValidationStatus === null &&
        this.releaseDateValidationStatus === null &&
        this.filmDescriptionValidationStatus === null &&
        this.trailerLinkValidationStatus === null &&
        this.SEOURLValidationStatus === null &&
        this.SEOTitleValidationStatus === null &&
        this.SEOKeywordsValidationStatus === null &&
        this.SEODescriptionValidationStatus === null &&
        this.typeFilmValidationStatus === null &&
        this.mainPictureValidationStatus === null
      )
    },
    async Save () {
      this.$emit('isLoading', true)
      if (this.ListDeleteImages) {
        for (let i = 0; i < this.ListDeleteImages.length; i++) {
          this.cloudStorage.DeleteFileInFirebaseStorage(this.fileFolderPath, this.ListDeleteImages[i])
        }
        delete this.ListDeleteImages
      }
      if (this.validateTypeError() & this.validate()) {
        if (this.mainPicture.src) {
          this.mainPicture.link = await this.cloudStorage.UploadFile(this.fileFolderPath, this.mainPicture)
          this.saveListImgTriger = !this.saveListImgTriger
          delete this.mainPicture.src
        } else {
          delete this.mainPicture
        }
        if (this.gallary.length) {
          for (let i = 0; i < this.gallary.length; i++) {
            if (this.gallary[i].src) {
              this.gallary[i].link = await this.cloudStorage.UploadFile(this.fileFolderPath, this.gallary[i])
              delete this.gallary[i].src
            }
          }
        }
        const docData = {
          filmName: this.filmName,
          releaseDate: this.releaseDate,
          filmDescription: this.filmDescription,
          typeFilm: this.typeFilm,
          SEO: this.SEO,
          gallary: this.gallary,
          mainPicture: this.mainPicture,
          trailerLink: this.trailerLink
        }
        this.cloudFirestore.AddDocument(this.collectionRef, this.id, docData)
      }
      this.$emit('isLoading', false)
    },
    validateTypeError () {
      if (
        this.type3D === false &&
        this.type2D === false &&
        this.typeIMAX === false
      ) {
        this.TypeError = true
        return false
      } else {
        this.TypeError = false
        return true
      }
    },
    changeLoading (value) {
      this.isLoading = value
    },
    GetFilm () {
      this.ListDeleteImages = []
      this.cloudFirestore.getDocument(this.collectionRef, this.$route.params.id).then((document) => {
        if (document) {
          Object.assign(this.$data, document)
        }
      })
    }
  },
  watch: {
    typeFilm () {
      this.validateTypeError()
    },
    deleteMainPicture () {
      console.log(this.deleteMainPicture)
    }
  }
}
</script>

<style>
.error-message {
  color: red;
  font-size: 0.8em;
  margin-top: 0.2em;
}

.is-invalid {
  border: 1px solid red;
  border-radius: 5px;
}
</style>
